















import Vue from 'vue'

type Data = {
  navItem: string[]
}
export default Vue.extend({
  name: 'Header',
  data: function (): Data {
    return {
      navItem: []
    }
  },
  methods: {
    async logout () {
      try {
        this.$store.dispatch('LOGOUT')
        alert('로그아웃 되었습니다')
        this.$router.push('/').catch((err) => err)
      } catch (error) {
        alert('error')
      }
    }
  },
  computed: {
    isLogin () {
      return this.$store.state.Auth.login
    },
    payload () {
      return this.$store.state.Auth.profile || {}
    }
  }
})
