











































































import Vue from 'vue'
import dayjs from 'dayjs'

export default Vue.extend({
  name: 'Home',
  data: () => {
    return {
      time: '',
      sec: '',
      noon: ''
    }
  },
  mounted: function () {
    this.clock()
    setInterval(this.clock, 1000)
  },
  components: {
  },
  methods: {
    clock: function () {
      const now = dayjs()
      this.time = now.format('hh:mm')
      this.sec = now.format('ss')
      this.noon = now.format('A')
    }
  }
})
